/* Fonts */
@font-face {
  font-family: "Kaushan Script";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/KaushanScript-Regular.woff2") format("woff2"), url("../fonts/KaushanScript-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto-Italic.woff") format("woff");
  font-display: swap;
}
/* Normalize */
body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Container */
.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Page */
.page__body {
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

/* Intro */
.intro {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  background-image: url(../img/intro/intro-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #e89584;
}

.intro__main {
  margin-top: 90px;
}

/* Header */
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Logo */
.logo__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 30px;
  text-decoration: none;
  color: #fff;
  padding: 34px 0;
}
.logo__link:hover {
  color: #fce38a;
  transition: color 0.3s linear;
}

/* Main-nav */
.main-nav__list {
  display: flex;
  align-items: center;
  width: 620px;
  max-width: 620px;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  line-height: 1.6;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  padding: 40px 0;
  position: relative;
}
.main-nav__link--current {
  color: #fce38a;
  cursor: pointer;
}
.main-nav__link--current::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fce38a;
  position: absolute;
  bottom: 33px;
  left: 0;
}
.main-nav__link:not(.main-nav__link--current):hover {
  color: #fce38a;
  transition: color 0.3s linear;
}
.main-nav__link:not(.main-nav__link--current)::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fce38a;
  position: absolute;
  bottom: 33px;
  left: 0;
  transform: scale(0);
}
.main-nav__link:not(.main-nav__link--current):hover::after {
  transform: scale(1);
  transition: transform 0.3s linear;
}
.main-nav__link:hover svg path {
  fill: #fce38a;
  transition: fill 0.3s linear;
}

/* Main */
.main {
  text-align: center;
}

.main__suptitle {
  font-family: "Kaushan Script", sans-serif;
  font-size: 72px;
  color: #fff;
  margin-bottom: 20px;
}

.main__title {
  max-width: 850px;
  margin: 0 auto;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 150px;
  text-transform: uppercase;
  line-height: 0.9;
  color: #fff;
}
.main__title::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #fff;
  margin: 65px auto 45px;
}

/* Button */
.button {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  border: 3px solid #fff;
  padding: 12px 30px;
}
.button:hover {
  background-color: #000;
  transition: background-color 0.3s linear;
}

/* Footer */
.footer {
  margin-top: auto;
}

.footer__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  width: 100%;
}

.footer__item {
  flex-basis: 25%;
  flex-grow: 1;
}

.footer__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  padding: 30px 0;
  position: relative;
}
.footer__link::before {
  content: "";
  display: block;
  width: 280px;
  height: 3px;
  background-color: #fff;
  position: absolute;
  top: 10px;
  left: 0;
}
.footer__link:not(.footer__link--current):hover {
  transition: color 0.3s linear;
  color: #4a4a4a;
}
.footer__link:not(.footer__link--current)::after {
  content: "";
  display: block;
  width: 70px;
  height: 3px;
  z-index: 2;
  background-color: #4a4a4a;
  position: absolute;
  top: 10px;
  left: 0;
  opacity: 0;
  transition: opacity 0.2s linear;
}
.footer__link:not(.footer__link--current):hover::after {
  opacity: 1;
}

.footer__link--current {
  color: #f38181;
}
.footer__link--current:after {
  content: "";
  display: block;
  width: 70px;
  height: 3px;
  z-index: 2;
  background-color: #f38181;
  position: absolute;
  top: 10px;
  left: 0;
}

.footer__item b {
  font-weight: 700;
  font-size: 24px;
}

/* Section */
.section--design {
  background-image: url("../img/design/design-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-bottom: 580px;
}

.section__wrapper {
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
}

.section__suptitle {
  font-family: "Kaushan Script", sans-serif;
  font-size: 24px;
  line-height: 1;
  color: #333;
  margin-top: 95px;
  margin-bottom: 25px;
}

.section__title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 1;
  text-transform: uppercase;
  color: #333;
}
.section__title::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #f38181;
  margin: 40px auto 45px;
}

.section__text {
  max-width: 950px;
  margin: 0 auto 90px;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 1.6;
  color: #999;
}

.section__list {
  margin: 0;
  padding: 0;
  padding-bottom: 120px;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.section__item {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.section__card {
  flex-basis: 33%;
}

.section__image-tablet {
  position: absolute;
  bottom: -580px;
  left: 333px;
}

.section__image-phone {
  position: absolute;
  bottom: -675px;
  left: 663px;
}

/* Gallery */
.gallery {
  margin-bottom: 110px;
}

.gallery__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.gallery__item {
  background-color: #95e1d3;
  position: relative;
}

.gallery__item:not(:last-child) {
  margin-right: 10px;
}

.gallery__card {
  background: linear-gradient(to bottom, #f38181, #fce38a);
  position: relative;
  transition: all 0.3s linear;
}
.gallery__card:hover {
  transform: translate(-10px, -10px);
}
.gallery__card:hover .gallery__image {
  opacity: 0.1;
}
.gallery__card:hover .gallery__team-icon {
  opacity: 1;
}
.gallery__card:hover .gallery__text {
  opacity: 1;
}

.gallery__image {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.gallery__team-icon {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}

.gallery__text {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
  color: #fff;
  position: absolute;
  top: 130px;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
}

/* Statistics */
.statistics {
  background-color: #95e1d3;
}

.statistics__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.statistics__item {
  text-align: center;
  flex-basis: 20%;
  padding: 80px 0;
  border-left: 1px solid #b5eae0;
}

.statistics__item:last-child {
  border-right: 1px solid #b5eae0;
}

.statistics__number {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 72px;
  text-transform: uppercase;
  color: #fff;
}

.statistics__text {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
}

/* Card */
.card {
  padding-left: 90px;
  padding-right: 35px;
  text-align: left;
  position: relative;
}

.card__image {
  position: absolute;
  top: 0;
  left: 33px;
}

.card__title {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  color: #333;
  margin-bottom: 13px;
}

.card__text {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 1.6;
  color: #999;
}

/* Rule */
.rule {
  width: 100%;
  border-top: 1px solid #999;
  margin: 50px 0;
  padding: 0;
  opacity: 0.5;
}

/* What we do */
.what-we-do {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.what-we-do__image {
  display: block;
  width: 100%;
  max-width: 570px;
  height: auto;
  object-fit: contain;
  margin: 0 15px 15px 15px;
}

.what-we-do__list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: left;
  min-width: 360px;
  max-width: 570px;
  flex-grow: 1;
}

.what-we-do__item {
  position: relative;
}

.what-we-do__title {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  color: #333;
  border: 1px solid #e5e5e5;
  padding: 20px 60px;
  position: relative;
}
.what-we-do__title--opened ~ .what-we-do__text {
  display: block;
}
.what-we-do__title--closed ~ .what-we-do__text {
  display: none;
}
.what-we-do__title::before {
  content: "";
  width: 30px;
  height: 30px;
  background-image: url("../img/wedo/photography.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  top: 13px;
  left: 15px;
}
.what-we-do__title--creativity::before {
  background-image: url("../img/wedo/creativity.svg");
}
.what-we-do__title--design::before {
  background-image: url("../img/wedo/web-design.svg");
}

.what-we-do__text {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-style: italic;
  color: #999;
  border: 1px solid #e5e5e5;
  border-top: none;
  padding: 20px 50px 20px 20px;
}

.what-we-do__button-toggle {
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: transparent;
  border: none;
  border-top: 2px solid #ccc;
  border-left: 2px solid #ccc;
  width: 20px;
  height: 20px;
  transform: rotate(45deg);
  position: absolute;
  top: 23px;
  right: 20px;
}

.what-we-do__title--closed + .what-we-do__button-toggle {
  transform: rotate(225deg);
  top: 15px;
}