.main-nav__list {
  display: flex;
  align-items: center;
  width: 620px;
  max-width: 620px;
  justify-content: space-between;

  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__link {
  display: block;

  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 1.6;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;

  padding: 40px 0;

  position: relative;

  &--current {
    color: #fce38a;

    cursor: pointer;
  }

  &--current::after {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background-color: #fce38a;

    position: absolute;
    bottom: 33px;
    left: 0;
  }

    &:not(&--current):hover {
    color: #fce38a;
    transition: color 0.3s linear;
  }

  &:not(&--current)::after {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background-color: #fce38a;

    position: absolute;
    bottom: 33px;
    left: 0;

    transform: scale(0);
  }

  &:not(&--current):hover::after {
    transform: scale(1);
    transition: transform 0.3s linear;
  }

  &:hover svg path {
    fill: #fce38a;
    transition: fill 0.3s linear;
  }
}


